import { Row, Col,Button, Card } from "react-bootstrap";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faArrowLeftLong,
  faIndianRupeeSign,
  faEraser,
  faArrowRightLong,
} from "@fortawesome/free-solid-svg-icons";
const CartReview = ({ handleKYCClick, cartItemsState,onRemoveItem,totalPrice, totalQuantity }) => {
  return (
    <>
    <Row className="CartBody">
      <Col xs="12" className="text-start py-2 ">
          <Link to="/store" style={{color:'goldenrod', fontSize:'16px'}}>
            <FontAwesomeIcon icon={faArrowLeftLong} />
          </Link>
      </Col>
    </Row>
    <Row className="cartCard">
    {cartItemsState.length !== 0  ? (cartItemsState.map((cartItem, index) => ( 
      <Card key={index} >
        <Row className="d-flex justify-content-center align-items-center border-bottom ">
          <Col xs="3">
            <Card.Img src={cartItem.product.image1} style={{ height: "100%", width: "100%"}} />
          </Col>
         <Col xs='6' style={{borderRight:"2px solid black", borderLeft:"2px solid black"}}>
         <Card.Title className="productTitle text-center">{cartItem.product.name}</Card.Title>
          <Card.Text className="productDesc text-center" as={Row}>
            <Col xs={6}>Quantity: {cartItem.quantity}</Col>
            <Col xs={6}>
              Price: {parseInt(cartItem.quantity)*parseInt(cartItem.product.price)}&nbsp;&nbsp;&nbsp;
              <FontAwesomeIcon icon={faIndianRupeeSign} />{" "}
            </Col>
          </Card.Text>
         </Col>
         <Col xs='3'>
          <Button  variant="outline-danger" className="ButtonSet" onClick={() => onRemoveItem(cartItem)} >
          <FontAwesomeIcon icon={faEraser} style={{fontSize:"10px"}} /> Remove item
          </Button>
         </Col>
        </Row>
      </Card>
     ))) : (<div className="text-center display-6 py-5 bg-light">
           your cart is empty.
          </div>)}
      

      
    </Row>
    <Row className="  pt-1 bg-dark">
      <Col xs="4" className="text-start " style={{color:'goldenrod', fontSize:'16px',border:'1px solid bottom goldenrod'}}>
      <div className="">Total Amount: {totalPrice} </div>
      </Col>
      <Col xs="4" className="text-center " style={{color:'goldenrod', fontSize:'16px',border:'1px solid bottom goldenrod'}}>
      <div className="">Total Quantity: {totalQuantity} </div>
      </Col>
     
      {cartItemsState.length !== 0 ? (
                      
                      <Col xs="4" className="text-end" >
          <Link onClick={handleKYCClick} style={{color:'goldenrod', fontSize:'16px'}} >
           Cart Reviewed <FontAwesomeIcon icon={faArrowRightLong}  />
          </Link>
      </Col>
                    
                  ) : (
                    <Col xs="4" className="text-end " >
                   
                   <Link to="/store" className="text-center" style={{color:'goldenrod', fontSize:'16px'}}>
            <FontAwesomeIcon icon={faArrowLeftLong} /> Store
          </Link>
                   
                </Col>
                  )}
    </Row>
  </>
  );
};

export default CartReview;
