import { Navigate } from "react-router-dom";
import { Row, Col, Container, Button, Card } from "react-bootstrap";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faShoppingCart,
  faContactCard,
  faCheck,
  faCheckDouble,
  faArrowLeftLong,
  faArrowRightLong
} from "@fortawesome/free-solid-svg-icons";
import CardHeader from "react-bootstrap/esm/CardHeader";
export default function CheckOut({handleKYCClick,handleSubmit,formData,totalQuantity,totalPrice,orderId,thankYou}) {

  return (
    <>
   {
    !thankYou ? ( <Container>
            <Row>

<Col sm="12" className="text-start py-2">
    <Link style={{color:'goldenrod', fontSize:'16px'}} onClick={handleKYCClick}>
      <FontAwesomeIcon icon={faArrowLeftLong} />
    </Link>
</Col>
</Row>
<Row className="cartCardd">
<Container>
  <Row>
    <Card>
      <CardHeader>
        <Card.Title>
          <span className="Display-1">#</span> Order Summary.
        </Card.Title>
      </CardHeader>
      <Card.Body>
          <Row>
          <Card.Text>
              Order Id: {orderId}
            </Card.Text>
            <Card.Text>
              Product List: {totalQuantity}
            </Card.Text>
            <Card.Text>
              Total Amount: {totalPrice}
            </Card.Text>

            <Card.Text>
              Shipping Details: {formData.Saddress}

            </Card.Text>
            <Card.Text>
              Shipping Instructions: {formData.anyInstructions}
            </Card.Text>
          </Row>
        </Card.Body>
    </Card>
  </Row>
</Container>
</Row>
<Row>
<Col sm="12" className="text-end pt-2">
  <Link style={{color:'goldenrod', fontSize:'16px'}} onClick={handleSubmit}>
     <FontAwesomeIcon icon={faCheck} /> Checkout
  </Link>
</Col>
</Row>
    </Container>):(<>
      <div className="get-in-touch">
            <div className="title fs-1">Your order-{orderId} Placed successfully. Thank you!</div>
            <div className="lead">you'll be redirected to home screen in 10 seconds.</div>
            </div>
    </>)
   }
    </>
  );
}