import React, { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPhone,
  faEnvelope,
  faMapMarkerAlt,
} from "@fortawesome/free-solid-svg-icons";
import ContactForm from "../components/ContactForm";
import "../css/genral.css";

const headerStyles = {
  fontFamily: "Georgia, serif",
  fontWeight: "bold",
  color: "#ffd700", // Accent color: gold
};

const displayStyles = {
  fontFamily: "Georgia, serif",
  fontWeight: "bold",
  color: "#ffd700", // Accent color: gold
};

const leadStyles = {
  fontFamily: "Arial, sans-serif",
  color: "black", // Light gray text
  lineHeight: "42px",
};

const contactInfoStyles = {
  fontFamily: "Arial, sans-serif",
  color: "black", // Light gray text
  paddingLeft: "12px",
};

const linkStyles = {
  color: "#ffd700", // Accent color: gold
  textDecoration: "none",
};

const About = () => {

  return (
    
    <Container className="mt-5">
      <div
        style={{ ...headerStyles, ...{ fontSize: "40px" } }}
        className="h1 text-md-end text-center"
      >
        About Us
      </div>
      <div
        style={{ ...leadStyles, ...{ padding: "20px" } }}
        className="p text-center"
      >
        Welcome to{" "}
        <span style={displayStyles} className="display-5 fw-bold fst-italic">
          Craft Tale Gifts
        </span>{" "}
        where passion meets craftsmanship, a{" "}
        <span
          style={{ ...displayStyles, ...{ fontSize: "24px" } }}
          className="fs-1"
        >
          legacy
        </span>{" "}
        spanning over 30 years in the art of creating extraordinary{" "}
        <span
          style={{
            ...headerStyles,
            ...{ fontSize: "24px", fontStyle: "italic" },
          }}
          className="fs-1"
        >
          Black Metal handicrafts
        </span>
        .Based in the vibrant city of{" "}
        <span
          style={{
            ...headerStyles,
            ...{ fontSize: "24px", fontStyle: "italic" },
          }}
          className="fs-1"
        >
          Hyderabad
        </span>{" "}
        Our journey is a blend of timeless passion and boundless creativity. At
        Craft Tale Gifts, we honor tradition while embracing innovation,
        infusing each creation with an enigmatic allure.
      </div>
      <hr></hr>
      <Container className="mt-5">
        <Row>
          <Col md={3}>
            <div
              style={{ ...headerStyles, ...{ fontSize: "40px" } }}
              className="h1 text-center"
            >
              Contact us
            </div>
          </Col>
          <Col md={3}>
            <h5 style={{  color: "green" }}>
              <FontAwesomeIcon icon={faPhone} /> Phone
            </h5>
            <p>
              <a
                href="tel:+9849251693"
                style={{ ...contactInfoStyles, ...linkStyles,color: "black" }}
              >
                9849251693
              </a>
            </p>
          </Col>
          <Col md={3}>
            <h5 style={{  color: "blueviolet" }}>
              <FontAwesomeIcon icon={faEnvelope} /> Mail
            </h5>
            <p>
              <a
                href="mailto:craftailgiftsb2b@gmail.com"
                style={{ ...contactInfoStyles, ...linkStyles,color: "black" }}
              >
                craftailgiftsb2b@gmail.com
              </a>
            </p>
          </Col>
          <Col md={3}>
            <h5 style={{  color: "orangered" }}>
              <FontAwesomeIcon icon={faMapMarkerAlt} /> Address
            </h5>
            <p style={{ ...contactInfoStyles}}>
              23-3-561, Sultan Shahi, Opp Moghalpura PS, Charminar, Hyderabad -
              500065
            </p>
          </Col>
        </Row>
      </Container>
      <hr></hr>
      
      <ContactForm />
    </Container>
  );
};

export default About;
