import { Row, Col, Container, Button, Form, FormLabel } from "react-bootstrap";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faArrowLeftLong,
  faArrowRightLong
} from "@fortawesome/free-solid-svg-icons";

export default function KYC({handleCartReviewClick, formData,handleChange,handleCheckoutClick}) {
  


  return (
    <>
      <Row>
        <Col sm="12" className="text-start py-2">
            <Link style={{color:'goldenrod', fontSize:'16px'}} onClick={handleCartReviewClick}>
              <FontAwesomeIcon icon={faArrowLeftLong} />
            </Link>
        </Col>
      </Row>
      <Row className="cartCardd ">
        <Form>
          <Row  className="d-flex justify-content-center align-items-center ">
            <FormLabel as={Col} xs='4' md='3' className="py-3 formText text-dark">Name :</FormLabel> <Col xs='8' md='9'><Form.Control placeholder="Full Name" name="name" 
            type="text"
              value={formData.name}
              onChange={handleChange}
            />
          </Col>
          <FormLabel as={Col} xs='4' md='3' className="py-3 formText text-dark">Email :</FormLabel> <Col xs='8' md='9'><Form.Control placeholder="Email" name="email" 
          type="email"
              value={formData.email}
              onChange={handleChange}
            />
          </Col>
          <FormLabel as={Col} xs='4' md='3' className="py-3 formText text-dark">Phone Number :</FormLabel> <Col xs='8' md='9'><Form.Control placeholder="phone number" name="phone" 
          type="number"
              value={formData.phone}
              onChange={handleChange}
            />
          </Col>
          <FormLabel as={Col} xs='4' md='3' className="py-3 formText text-dark">Billing Address :</FormLabel> <Col xs='8' md='9'><Form.Control placeholder="Billing Address" name="Baddress" 
          type="address"
              value={formData.Baddress}
              onChange={handleChange}
            />
          </Col>
         
           <FormLabel as={Col} xs='4' md='3' className="py-3 formText text-dark">Shipping Address :</FormLabel> <Col xs='8' md='9'><Form.Control placeholder="Shipping Address" name="Saddress" 
          type="address"
              value={formData.Saddress}
              onChange={handleChange}
            />
          </Col>
          
          <FormLabel as={Col} xs='4' md='3' className="py-3 formText text-dark">Any Instructions :</FormLabel> <Col xs='8' md='9'><Form.Control placeholder="Any Instructions" name="anyInstructions" 
              value={formData.anyInstructions}
              onChange={handleChange}
            />
          </Col>
          </Row>

    </Form>    </Row>
    <Row>
      <Col sm="12" className="text-end pt-2">
          <Link style={{color:'goldenrod', fontSize:'16px'}}   onClick={handleCheckoutClick}>
           Details Filled <FontAwesomeIcon icon={faArrowRightLong} />
          </Link>
      </Col>
      </Row>
    </>
  );
}