import React, { useState, useEffect } from "react";
import LoadingIcons from "react-loading-icons";
import Papa from "papaparse";
import {
  Container,
  Row,
  Col,
  Button,
  Form,
  FormControl,
  InputGroup,
  Dropdown,
  Carousel,
  Card,
} from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faShoppingCart,
  faAdd,
  faMinus,
  faSearch,
  faSort,
  faFilter,
  faShoppingBasket,
  faIndianRupeeSign,
} from "@fortawesome/free-solid-svg-icons";
// import ProductData from "../dummydata/ProductData";
import { Link, useNavigate } from "react-router-dom";
export default function ItemsList() {
  //items
  const [loading, setLoading] = useState(true);

  const [ProductData, setProductData] = useState([]);

  // State for search query
  const [searchQuery, setSearchQuery] = useState("");

  // State for selected options in filters
  const [selectedOptions, setSelectedOptions] = useState("nameAsc");
  const [selectedOptionsCat, setSelectedOptionsCat] = useState([]);
  const [selectedOptionsSize, setSelectedOptionsSize] = useState([]);
  const [selectedOptionsMet, setSelectedOptionsMet] = useState([]);

  // State for item counts in cart
  const [itemCounts, setItemCounts] = useState([]);

  // Effect to fetch data from Google Sheets

  useEffect(() => {
    Papa.parse(
      "https://docs.google.com/spreadsheets/d/e/2PACX-1vRvPP5veS1ZQAranTqGV-km0mgzb7Sw8_J6VElZ8SiWzAQgz5D7azoGyB7li5qvcXRoX6DBFJjZyC8S/pub?output=csv",
      {
        download: true,
        header: true,
        complete: (results) => {
          setProductData(results.data);

          // Initialize item counts to 0 for each product
          setItemCounts(Array(results.data.length).fill(0));
          setTimeout(() => {
            setLoading(false);
          }, 500);
        },
      }
    );
  }, []); // Empty dependency array ensures the effect runs once on mount
  //
  // const [searchQuery, setSearchQuery] = useState("");

  const CategoryTypes = Array.from(
    new Set(ProductData.map((product) => product.category))
  );
  const SizeTypes = Array.from(
    new Set(ProductData.map((product) => product.size))
  );
  const MetalTypes = Array.from(
    new Set(ProductData.map((product) => product.metal))
  );
  // const [selectedOptions, setSelectedOptions] = useState([]);
  const navigate = useNavigate();
  const filteredProducts = ProductData.filter((product) => {
    const nameMatch = product.name
      .toLowerCase()
      .includes(searchQuery.toLowerCase());
    const categoryMatch =
      selectedOptionsCat.length === 0 ||
      selectedOptionsCat.includes(product.category);
    const sizeMatch =
      selectedOptionsSize.length === 0 ||
      selectedOptionsSize.includes(product.size);
    const metalMatch =
      selectedOptionsMet.length === 0 ||
      selectedOptionsMet.includes(product.metal);
    return nameMatch && categoryMatch && sizeMatch && metalMatch;
  });
  const handleOptionChange = (selectedOption) => {
    let sortedProducts = [...ProductData];

    switch (selectedOption) {
      case "plph":
        sortedProducts.sort((a, b) => a.price - b.price);
        break;
      case "phpl":
        sortedProducts.sort((a, b) => b.price - a.price);
        break;
      case "nameAsc":
        sortedProducts.sort((a, b) => a.name.localeCompare(b.name));
        break;
      case "nameDesc":
        sortedProducts.sort((a, b) => b.name.localeCompare(a.name));
        break;
      default:
        break;
    }

    setProductData(sortedProducts);
  };
  const handleOptionChangeCat = (x) => {
    const newSelectedOptions = [...selectedOptionsCat];
    const index = newSelectedOptions.indexOf(x);

    if (index > -1) {
      newSelectedOptions.splice(index, 1);
    } else {
      newSelectedOptions.push(x);
    }
    setSelectedOptionsCat(newSelectedOptions);
  };
  const handleOptionChangeSize = (x) => {
    const newSelectedOptions = [...selectedOptionsSize];
    const index = newSelectedOptions.indexOf(x);

    if (index > -1) {
      newSelectedOptions.splice(index, 1);
    } else {
      newSelectedOptions.push(x);
    }
    setSelectedOptionsSize(newSelectedOptions);
  };
  const handleOptionChangeMet = (x) => {
    const newSelectedOptions = [...selectedOptionsMet];
    const index = newSelectedOptions.indexOf(x);

    if (index > -1) {
      newSelectedOptions.splice(index, 1);
    } else {
      newSelectedOptions.push(x);
    }
    setSelectedOptionsMet(newSelectedOptions);
  };

  const quantity = itemCounts.filter((item) => item >= 10).length;

  const handleAddToCartClick = (product) => {
    setItemCounts((prevItemCounts) => {
      const updatedItemCounts = [...prevItemCounts];
      updatedItemCounts[product.id - 1] =
        updatedItemCounts[product.id - 1] + 10;
      setItemCounts(updatedItemCounts);

      return updatedItemCounts;
    });
  };
  const handleCheckout = () => {
    // Navigate to the cart page and pass cartItems as state
    const cartItems = filteredProducts
      .filter((product) => itemCounts[product.id - 1] >= 10)
      .map((product) => ({
        product,
        quantity: itemCounts[product.id - 1],
      }));

    navigate("/cart", { state: cartItems });
    console.log(cartItems);
  };

  return (
    <>
      {loading ? (
        <Row className="d-flex justify-content-center align-items-center loader">
          <LoadingIcons.Bars
            stroke="#ffd700"
            fill="#ffd700"
            fillOpacity=".25"
          />
        </Row>
      ) : (
        <>
          {" "}
          <Container className="sticky-top">
            <Row className="justify-content-center">
              <Col sm={6}>
                <Form>
                  <InputGroup>
                    <InputGroup.Text className="navItems">
                      <FontAwesomeIcon icon={faSearch} />
                    </InputGroup.Text>
                    <FormControl
                      type="search"
                      placeholder="Search"
                      className="navItems"
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                    />
                    <Dropdown>
                      <Dropdown.Toggle variant="light" id="dropdown-basic">
                        <FontAwesomeIcon icon={faFilter} />
                      </Dropdown.Toggle>
                      <Dropdown.Menu className="DBmenu">
                        <Dropdown.Header className="DBmenuTxt">
                          Category :
                        </Dropdown.Header>
                        <Container>
                          <Row>
                            <Col className="">
                              {CategoryTypes.map((ct) => (
                                <div key={ct} className="form-check">
                                  <input
                                    type="checkbox"
                                    className="form-check-input"
                                    id={ct}
                                    checked={selectedOptionsCat.includes(ct)}
                                    onChange={() => {
                                      handleOptionChangeCat(ct);
                                    }}
                                  />
                                  <label
                                    className="form-check-label"
                                    htmlFor={ct}
                                  >
                                    {ct}
                                  </label>
                                </div>
                              ))}
                            </Col>
                          </Row>
                        </Container>
                        <Dropdown.Header className="DBmenuTxt">
                          Size :
                        </Dropdown.Header>
                        <Container>
                          <Row>
                            <Col className="">
                              {SizeTypes.map((st) => (
                                <div key={st} className="form-check">
                                  <input
                                    type="checkbox"
                                    className="form-check-input"
                                    id={st}
                                    checked={selectedOptionsSize.includes(st)}
                                    onChange={() => handleOptionChangeSize(st)}
                                  />
                                  <label
                                    className="form-check-label"
                                    htmlFor={st}
                                  >
                                    {st}
                                  </label>
                                </div>
                              ))}
                            </Col>
                          </Row>
                        </Container>
                        <Dropdown.Header className="DBmenuTxt">
                          Metal :
                        </Dropdown.Header>
                        <Container>
                          <Row>
                            <Col className="">
                              {MetalTypes.map((mt) => (
                                <div key={mt} className="form-check">
                                  <input
                                    type="checkbox"
                                    className="form-check-input"
                                    id={mt}
                                    checked={selectedOptionsMet.includes(mt)}
                                    onChange={() => handleOptionChangeMet(mt)}
                                  />
                                  <label
                                    className="form-check-label"
                                    htmlFor={mt}
                                  >
                                    {mt}
                                  </label>
                                </div>
                              ))}
                            </Col>
                          </Row>
                        </Container>
                      </Dropdown.Menu>
                    </Dropdown>
                    <Dropdown>
                      <Dropdown.Toggle variant="light" >
                        <FontAwesomeIcon icon={faSort} />
                      </Dropdown.Toggle>

                      <Dropdown.Menu className="DBmenu">
                        <Row>
                          <Col xs={"3"}>
                            <input
                              type="radio"
                              className=""
                              value="plph"
                              checked={selectedOptions === "plph"}
                              onChange={() => {
                                handleOptionChange("plph");
                                setSelectedOptions("plph");
                              }}
                            />
                          </Col>

                          <Col xs={"9"}>

                            <label className="">

                              Price: Low to High
                            </label>
                          </Col>
                        </Row>

                        <Row>
                          <Col xs={"3"}>
                            <input
                              type="radio"
                              className=""
                              value="phpl"
                              checked={selectedOptions === "phpl"}
                              onChange={() => {
                                handleOptionChange("phpl");
                                setSelectedOptions("phpl");
                              }}
                            />
                          </Col>
                          <Col xs={"9"}>
                            <label className="">
                              Price: High to Low
                            </label>
                          </Col>
                        </Row>

                        <Row>
                          <Col xs={"3"}>
                            <input
                              type="radio"
                              className=""
                              value="nameAsc"
                              checked={selectedOptions === "nameAsc"}
                              onChange={() => {
                                handleOptionChange("nameAsc");
                                setSelectedOptions("nameAsc");
                              }}
                            />
                          </Col>
                          <Col xs={"9"}>

                            <label className="">

                              Name Assending
                            </label>
                          </Col>
                        </Row>
                        <Row>
                          <Col xs={"3"}>

                            <input
                              type="radio"
                              className=""
                              value="nameDesc"
                              checked={selectedOptions === "nameDesc"}
                              onChange={() => {
                                handleOptionChange("nameDesc");
                                setSelectedOptions("nameDesc");
                              }}
                            />
                          </Col>
                          <Col xs={"9"}>
                            <label className="">
                              Name Dessending
                            </label>
                          </Col>
                        </Row>
                      </Dropdown.Menu>
                    </Dropdown>
                    {quantity > 0 ? (
                      <Button
                        className="navItems"
                        variant="light"
                        onClick={handleCheckout}
                      >
                        <FontAwesomeIcon icon={faShoppingBasket} />
                        <span className="text-on-icon"> {quantity} </span>
                      </Button>
                    ) : (
                      <Button variant="light" disabled className="navItems">
                        <FontAwesomeIcon icon={faShoppingBasket} />
                        <span className="text-on-icon">&nbsp;&nbsp;</span>
                      </Button>
                    )}
                  </InputGroup>
                </Form>
              </Col>
            </Row>

            <br />
          </Container>
          <Container className="mb-5">
            <Row className="justify-content-md-baseline justify-content-center">
              {filteredProducts.length > 0 ? (
                filteredProducts.map((product) => (
                  <Col
                    key={product.id}
                    xs={6}
                    sm={6}
                    md={4}
                    lg={3}
                    className="pt-5"
                  >
                    <Card
                      className="shadow h-100"
                      style={{ width: "100%", border: "0" }}
                    >
                      <Carousel
                        interval={null}
                        touch="true"
                        data-bs-theme="dark"
                        className="carousel"
                        variant="dark"
                        controls={true}
                        indicators={true}
                        style={{ border: "1px solid white" }}
                      >
                        <Carousel.Item>
                          <Card.Img
                            variant="top"
                            src={product.image1}
                            style={{ height: "100%", width: "100%" }}
                          />
                        </Carousel.Item>
                        <Carousel.Item>
                          <Card.Img
                            variant="top"
                            src={product.image2}
                            style={{ height: "100%", width: "100%" }}
                          />
                        </Carousel.Item>
                        <Carousel.Item>
                          <Card.Img
                            variant="top"
                            src={product.image3}
                            style={{ height: "100%", width: "100%" }}
                          />
                        </Carousel.Item>
                      </Carousel>

                      <Card.Body className=" align-items-center">
                        <Card.Title>
                          <Row>
                            <Col xs={10}>
                              <Row className="justify-content-start px-3 fs-6">
                                {product.name}
                              </Row>
                            </Col>
                            <Col xs={2}>
                              <Row className="justify-content-center  fs-6">
                                "{product.productCode}"
                              </Row>
                            </Col>
                          </Row>
                        </Card.Title>
                        <Card.Text className="productDesc " as={Row}>
                          <Col xs={6}>Size: {product.size}</Col>
                          <Col xs={6}>Weight: {product.weight} Kg </Col>
                          <Col xs={6}>Metal: {product.metal} </Col>
                          <Col xs={6}>
                            Price: {product.price}&nbsp;&nbsp;&nbsp;
                            <FontAwesomeIcon icon={faIndianRupeeSign} />{" "}
                          </Col>
                        </Card.Text>
                        <Row className="pt-2 px-md-3 d-flex align-items-center">
                          {/* Conditionally render based on quantity */}
                          {itemCounts[product.id - 1] >= 10 ? (
                            <>
                              <Col xs="4">
                                <Button
                                  className="cartValueB"
                                  variant="outline-dark"
                                  size="sm"
                                  onClick={() => {
                                    setItemCounts((prevItemCounts) => {
                                      const updatedItemCounts = [
                                        ...prevItemCounts,
                                      ];
                                      updatedItemCounts[product.id - 1] =
                                        Math.max(
                                          updatedItemCounts[product.id - 1] - 1,
                                          0
                                        );
                                      if (
                                        updatedItemCounts[product.id - 1] <= 9
                                      ) {
                                        updatedItemCounts[product.id - 1] = 0;
                                      }
                                      return updatedItemCounts;
                                    });
                                  }}
                                >
                                  <FontAwesomeIcon icon={faMinus} />
                                </Button>
                              </Col>
                              <Col xs="4">
                                <i className="cartValue">
                                  {itemCounts[product.id - 1]}
                                </i>
                              </Col>
                              <Col xs="4">
                                <Button
                                  className="cartValueB"
                                  variant="outline-dark"
                                  size="sm"
                                  onClick={() => {
                                    setItemCounts((prevItemCounts) => {
                                      const updatedItemCounts = [
                                        ...prevItemCounts,
                                      ];
                                      updatedItemCounts[product.id - 1] += 1;
                                      return updatedItemCounts;
                                    });
                                  }}
                                >
                                  <FontAwesomeIcon icon={faAdd} />
                                </Button>
                              </Col>
                            </>
                          ) : (
                            <Button
                              variant="outline-dark"
                              onClick={() => handleAddToCartClick(product)}
                              className="ButtonSet"
                            >
                              <FontAwesomeIcon icon={faShoppingCart} /> Add to
                              Cart
                            </Button>
                          )}
                        </Row>
                      </Card.Body>
                    </Card>
                  </Col>
                ))
              ) : (
                <div className="text-center p-5 mt-5 lead fs-1">
                  Soon to be added.
                </div>
              )}
            </Row>
          </Container>
          <br></br>

        </>
        
      )}
    </>
  );
}
