import Papa from "papaparse";
import LoadingIcons from "react-loading-icons";
import { useState,useEffect } from "react";
export default function Home() {
    const [loading, setLoading] = useState(true);
    const [data,setData] = useState([]);
    useEffect(() => {
        Papa.parse(
          "https://docs.google.com/spreadsheets/d/e/2PACX-1vRh5DgnE2mp0gjiEFWWQmi5IItNhzv07YK9zwTIy3qbQSOLCg7xoWA8nTH65KW-2yescvPEQyPd0AKM/pub?gid=0&single=true&output=csv",
          {
            download: true,
            header: true,
            complete: (results) => {
                setData(results.data);

                setTimeout(() => {
            setLoading(false);
          }, 5000);
            },
          }
        );
      }, []);
  return (
    <>
    {loading ? (
      <div className="d-flex justify-content-center align-items-center loader h-100">
        <LoadingIcons.Bars
          stroke="#ffd700"
          fill="#ffd700"
          fillOpacity=".25"
        />
      </div>
    ) : (
        <>{
            data.map((dataa) => (<div>
                <div className="container h-100 text-light ">
                    <div className="row d-flex align-items-center p-md-5 mt-md-2 p-2 mt-1">
                      <div className="col-md-5 col-12">
                        <div className="row justify-content-center">
                          <img
                            src={dataa.image1}
                            className="w-70"
                            style={{mixBlendMode:'multiply'}}
                          ></img>
                        </div>
                      </div>
                      <div className="col-md-7 col-12">
                        <div className="row justify-content-center">
                          <div className="display-6 mb-4 text-center text-warning">
                            {dataa.heading1}
                          </div>
                          <div className="p text-center fst-italic text-dark fs-6 mb-4 px-md-5 px-1">
                            {dataa.body1}
                          </div>
                          <button type="button" className="btn btn-outline-warning ">
                            Buy Now
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                  <hr className="container"></hr>
                  <div className="container h-100 text-light ">
                    <div className="row d-flex align-items-center p-5">
                      <div className="col-md-8 col-12 order-2">
                        <div className="container">
                          <div className="row mt-2">
                            <a className="col-md-4">
                              <img
                                src={dataa.bs1}
                                className="img-fluid rounded"  style={{mixBlendMode:'multiply'}}
                              ></img>
                            </a>
                            <a className="col-md-4">
                              <img
                                src={dataa.bs2}
                                className="img-fluid rounded"  style={{mixBlendMode:'multiply'}}
                              ></img>
                            </a>
                            <a className="col-md-4">
                              <img
                                src={dataa.bs3}
                                className="img-fluid rounded"  style={{mixBlendMode:'multiply'}}
                              ></img>
                            </a>
                          </div>
                          <div className="row mt-3">
                            <a className="col-md-4">
                              <img
                                src={dataa.bs4}
                                className="img-fluid rounded"  style={{mixBlendMode:'multiply'}}
                              ></img>
                            </a>
                            <a className="col-md-4">
                              <img
                                src={dataa.bs5}
                                className="img-fluid rounded"  style={{mixBlendMode:'multiply'}}
                              ></img>
                            </a>
                            <a className="col-md-4">
                              <img
                                src={dataa.bs6}
                                className="img-fluid rounded"  style={{mixBlendMode:'multiply'}}
                              ></img>
                            </a>
                          </div>
                        </div>
                      </div>
                      <div className="col-md-4 col-12">
                        <div className="row justify-content-center">
                          <div className="display-6 mb-4 text-start text-warning">
                            {dataa.heading2}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>))}
        
        </>
    )}
  </>
    
  );
}