import { Col, Container, Row } from "react-bootstrap";
import { useState } from "react";

export default function ContactForm() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phone: "",
    message: "",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();



    const formUrl =
      "https://docs.google.com/forms/d/e/1FAIpQLSdJPJq_ua47DKTsuPHu0DDXHr7Hh61lHx9DcdhCWwi-7_7bkg/formResponse";

    const data = new FormData();
    data.append("entry.2005620554", formData.name);
    data.append("entry.1045781291", formData.email);
    data.append("entry.1166974658", formData.phone);
    data.append("entry.839337160", formData.message);

    try {
      await fetch(formUrl, {
        method: "POST",
        body: data,
        mode: "no-cors",
      });
  
      // Call thankyouMessage after the form is successfully submitted
      thankyouMessage();
      
      // Reset form data
      setFormData({
        name: "",
        email: "",
        phone: "",
        message: "",
      });
    } catch (error) {
      console.error("Form submission error:", error);
    }
  };
  const [thankYou, setThankyou] = useState(false);

  const thankyouMessage = ()=>{
    setThankyou(true);
    setTimeout(() => {
      setThankyou(false);
    }, 5000);
  }
  return (
   <>
   {
    !thankYou ? ( <Container>
      <Row>
        <Col sm={12}>
          <div className="get-in-touch">
            <h1 className="title">Get in touch</h1>
            <form className="contact-form row  text-center" onSubmit={handleSubmit}>
              <div className="col-md-6 c0l-12 p-3">
                <input
                  name="name"
                  value={formData.name}
                  onChange={handleChange}
                  placeholder="Your Name"
                  type="text"
                  required=""
                />
              </div>
              <div className="col-md-6 col-12 p-3 ">
                <input
                  name="email"
                  value={formData.email}
                  onChange={handleChange}
                  placeholder="enter your Email"
                  type="email"
                  required=""
                />
              </div>
              <div className=" col-md-6 col-12 p-3">
                <input
                  name="phone"
                  value={formData.phone}
                  onChange={handleChange}
                  placeholder="enter your Contact Number"
                  type="number"
                  required=""
                />
              </div>
              <div className="col-md-6 col-12 p-3">
                <input
                  name="message"
                  value={formData.message}
                  onChange={handleChange}
                  placeholder="Your Message"
                  type="text"
                  required=""
                />
              </div>
              <div className="col-12  text-center py-3">
                <input className="submit-btn" type="submit" defaultValue="Submit" />
              </div>
            </form>
          </div>
        </Col>
      </Row>
    </Container>):(<>
      <div className="get-in-touch">
            <div className="title fs-1">Thank you! we'll get back to you</div>
            </div>
    </>)
   }
   </>
  );
}
