import { Navigate, useNavigate,useLocation  } from 'react-router-dom';
import React, { useState,useEffect } from "react";
import { Row, Col, Container, Button } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faShoppingCart,
  faContactCard,
  faCheck,
} from "@fortawesome/free-solid-svg-icons";
import CartReview from "../components/CartReview";
import KYC from "../components/KYC";
import CheckOut from "../components/CheckOut";

export default function Cart() {

 const location = useLocation();
  const cartItems = location.state;

  const [cartItemsState, setCartItemsState] = useState([]);
  const [totalPrice, setTotalPrice] = useState();
  const [totalQuantity, setTotalQuantity] = useState();

  
  useEffect(() => {
    // Set cartItemsState when cartItems changes
    const storedCartItems = JSON.parse(localStorage.getItem('cartItems'));
    setCartItemsState(cartItems || storedCartItems ||  []);
    // Save cart items to local storage when cartItemsState changes
    localStorage.setItem('cartItems', JSON.stringify(cartItemsState));

  }, [cartItems]);

  useEffect(() => {
    // Calculate totals when cartItemsState changes
    const calculateTotals = () => {
      const calculatedTotalPrice = cartItemsState.reduce(
        (total, cartItem) =>
          total + parseInt(cartItem.quantity) * parseInt(cartItem.product.price),
        0
      );

      const calculatedTotalQuantity = cartItemsState.reduce(
        (total, cartItem) => total + parseInt(cartItem.quantity),
        0
      );

      setTotalPrice(calculatedTotalPrice);
      setTotalQuantity(calculatedTotalQuantity);
    };

    calculateTotals();
    
  }, [cartItemsState ]);
  

  const Navigate = useNavigate();
  const [cartreviewB, setCartreviewB] = useState(true);
  const [kycB, setkycB] = useState(false);
  const [checkoutB, setCheckoutB] = useState(false);

  const handleRemoveItem = (itemToRemove) => {
    const updatedCartItems = cartItemsState.filter((item) => item !== itemToRemove);
    // Save updated cart items to local storage
    setCartItemsState(updatedCartItems);
  };
  const productsList = cartItemsState.map(cartItem => `${cartItem.product.name} (${cartItem.product.productCode}) (${cartItem.quantity})`);
  const Flist =productsList.join(',');
  const [formData, setFormData] = useState({
    orderId:"",
    name: "",
    email: "",
    phone: "",
    productsList:"",
    Baddress: "",
    Saddress:"",
    anyInstructions:"",
    totalCost:"",
  });
  const [thankYou, setThankyou] = useState(false);

  const thankyouMessage = ()=>{
    setThankyou(true);
    setTimeout(() => {
      Navigate('/');
    }, 10000);
  }
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };
    const [orderId, setOrderId] = useState('');
  
    const generateOrderId = () => {
      const currentDate = new Date();
      const timestamp = currentDate.getTime();
      const orderId = `ORDER-${timestamp}`;
      setOrderId(orderId);
      setFormData({
        ...formData,
        orderId: orderId,
        productsList:Flist,
        totalCost:totalPrice,
      });
    };
  const handleSubmit = async (e) => {
    e.preventDefault();


   
    const formUrl ="https://docs.google.com/forms/d/e/1FAIpQLSeMGE5gjqjrVODR3ExDSNTMV7G0vnGcL9veWJJkHVMhF2SpXA/formResponse";

    const data = new FormData();
    data.append("entry.141120267", formData.orderId);
    data.append("entry.2044239516", formData.name);
    data.append("entry.1944987973", formData.email);
    data.append("entry.809584760", formData.phone);
    data.append("entry.186012113", formData.productsList);
    data.append("entry.978156796", formData.Baddress);
    data.append("entry.1055215754", formData.Saddress);
    data.append("entry.547581104", formData.totalCost);
    data.append("entry.1639920459", formData.anyInstructions);


    try {
      await fetch(formUrl, {
        method: "POST",
        body: data,
        mode: "no-cors",
      });
    
    }catch (error) {
      console.error("Form submission error:", error);
    }CartSubmit();}
  // console.log(formData);
  // 

 
  const handleCartReviewClick = () => {
    setCartreviewB(true);
    setkycB(false);
    setCheckoutB(false);
  };

  const handleKYCClick = () => {
    setCartreviewB(false);
    setkycB(true);
    setCheckoutB(false);
    generateOrderId();   
  };

  const handleCheckoutClick = () => {
    setCartreviewB(false);
    setkycB(false);
    setCheckoutB(true);

  };
  const CartSubmit =()=>{
    thankyouMessage(); 
  }
  return (
    <section>
       
      <Container>
        <Row className="justify-content-center">
          <Col xs="12" sm="8" md="6">
            <Row className="text-center cartNav">
              <Col xs="4">
                <div
                >
                  <FontAwesomeIcon className="cartNavItems" icon={faShoppingCart} 
                  style={{color: cartreviewB?'goldenrod':'gray',fontSize: cartreviewB?'22px':'20px' }}
                  />
                </div>
              </Col>
              <Col xs="4">
                <div
                >
                  <FontAwesomeIcon className="cartNavItems" icon={faContactCard} style={{color: kycB?'goldenrod':'gray',fontSize: kycB?'22px':'20px' }} />
                </div>
              </Col>
              <Col xs="4">
                <div
                >
                  <FontAwesomeIcon className="cartNavItems" icon={faCheck} style={{color: checkoutB?'goldenrod':'gray',fontSize: checkoutB?'22px':'20px' }}/>
                </div>
              </Col>
            </Row>
            {cartreviewB && <CartReview handleKYCClick={handleKYCClick} cartItemsState={cartItemsState} onRemoveItem={handleRemoveItem} totalPrice={totalPrice} totalQuantity={totalQuantity}/>}
            {kycB && <KYC handleCartReviewClick={handleCartReviewClick} handleCheckoutClick={handleCheckoutClick} formData={formData}handleChange={handleChange} />}
            {checkoutB && <CheckOut  handleKYCClick={handleKYCClick} CartSubmit={CartSubmit} totalPrice={formData.totalCost} totalQuantity={formData.productsList}  formData={formData} orderId={orderId} cartItemsState={cartItemsState} handleSubmit={handleSubmit} thankYou={thankYou}/>}
          </Col>
        </Row>
      </Container>
    </section>
  );
}